.main {
   @apply text-white;
   & h4 {
      @apply font-bold text-center mb-6 lg:mb-10 text-5xl lg:text-6xl;
   }
   & form {
      @apply flex flex-col gap-y-4 lg:max-w-[800px] mx-auto;
      & input {
         &.invalid {
            @apply border-Red border-2;
         }
         @apply bg-primary px-3 py-2 text-lg lg:text-xl opacity-[0.72] rounded-xl;
         &:last-child {
            @apply opacity-100 font-bold bg-Blue lg:hover:scale-[1.075] transition-transform;
         }
      }
      & .error {
         @apply text-Red relative bottom-3 left-3;
      }
   }
}
